@font-face {
    font-family: 'Baloo Paaji 2';
    src: url('BalooPaaji2-SemiBold.eot');
    src: url('BalooPaaji2-SemiBold.eot?#iefix') format('embedded-opentype'),
        url('BalooPaaji2-SemiBold.woff2') format('woff2'),
        url('BalooPaaji2-SemiBold.woff') format('woff'),
        url('BalooPaaji2-SemiBold.ttf') format('truetype'),
        url('BalooPaaji2-SemiBold.svg#BalooPaaji2-SemiBold') format('svg');
    font-weight: 600;
    font-style: normal;
}

